"use client";

import React, { useEffect, useState } from "react";
import { UserCheck } from "@phosphor-icons/react";
import Grid from "@mui/material/Grid2";
import useTheme from "@mui/material/styles/useTheme";
import { Box, Button, FormControl, TextField, Typography } from "@mui/material";

import { BaseButton } from "@/components/Button";
import Loading from "./Loading";
import {
  initializeLiff,
  registerUserByTaxId,
} from "../services/regisger_service";
import { Controller, useForm } from "react-hook-form";
import { SignInFormData, SignInSchema } from "@/schemas/SignInSchema";
import { zodResolver } from "@hookform/resolvers/zod";

const RegisterForm = () => {
  const liffId = process.env.NEXT_PUBLIC_LIFF_ID || "";
  const [isIintLine, setIintLine] = useState(false);
  const [isLoading, setLoading] = React.useState(false);
  const theme = useTheme();
  const u = theme.palette.background.default;
  let userId = "U9c35e388d140d7173c08374ca65cc035";
  useEffect(() => {
    setLoading(true);
    initializeLiff({ setIintLine, setLoading, liffId });
  }, []);

  const regisger = async (data: SignInFormData) => {
    setLoading(true);
    try {
      var response = await registerUserByTaxId(data.tax_id, data.user_id);
      console.log(response);
      console.log(response.status);
      console.log(response.data);
    } catch (e) {
      console.log(e);
    } finally {
      console.log("finally");

      setLoading(false);
    }
  };

  const {
    register,
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<SignInFormData>({
    // mode: "all",
    // resolver: zodResolver(SignInSchema),
  });

  const onSubmit = async (data: SignInFormData) => {
    data.user_id = userId;
    await regisger(data);
  };

  return (
    <Box>
      <Loading open={isLoading} />
      {isIintLine ? (
        <Grid
          container
          spacing={1}
          alignItems="center"
          justifyContent="center"
          display="flex"
          height="100vh"
          sx={{ backgroundColor: u }}
        >
          <Grid size={{ xs: 12, md: 6 }} sx={{ px: { xs: 5, md: 0 } }}>
            <Box
              sx={{ py: 5, bgcolor: "white", px: 5, borderRadius: 4 }}
              component="form"
              onSubmit={handleSubmit(onSubmit)}
            >
              <Typography py={1.5} align="center" sx={{ fontSize: "1rem" }}>
                ลงทะเบียน
              </Typography>

              <FormControl fullWidth>
                <Controller
                  name="tax_id"
                  control={control}
                  rules={{
                    required: "กรุณาระบุจำนวนลิตรที่เติม",
                    // pattern: {
                    //   value: /^\d+(\.\d{1,3})?$/,
                    //   message:
                    //     "ระบุเฉพาะตัวเลขจำนวนเต็ม หรือทศนิยมไม่เกิน 3 ตำแหน่ง เช่น 999 หรือ 999.999",
                    // },
                  }}
                  render={({ field: { onChange, value } }) => (
                    <TextField
                      id="standard-basic"
                      variant="outlined"
                      placeholder="เลขประจำตัวผู้เสียภาษี"
                      helperText={errors.tax_id?.message}
                      error={!!errors.tax_id}
                      fullWidth
                      onChange={onChange}
                      value={value || ""}
                      slotProps={{
                        input: {
                          endAdornment: <UserCheck size={30} />,
                        },
                      }}
                    />
                  )}
                />
              </FormControl>

              <Box mt={2}>
                <BaseButton
                  text="ลงทะเบียน"
                  color="primary"
                  variant="contained"
                  fullWidth
                  type="submit"
                  isProcessing={isLoading}
                />
              </Box>
            </Box>
          </Grid>
        </Grid>
      ) : null}
    </Box>
  );
};

export default RegisterForm;
